<template>
  <div class="vr-preview" v-if="imageList.length">
    <div class="bg-tag"/>
    <div class="preview-title">
      <span class="text">VR预览</span>
      <a href="javascript:;" @click="goPage()">更多 <i></i></a>
    </div>
    <div class="content">
      <div class="vr-item" v-for="(item, index) in imageList" :key="index" @click="toVrDetail(item, index)">
        <div class="program-play"/>
        <img :src="item.imgUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)"/>
        <p>{{item.title}}</p>
      </div>
    </div>
  </div>
</template>
<script>
/** 精选--VR预览 */
export default {
  name: 'vrPreview',
  props: {
    data: {
      type: Object,
      default: () => {},
    },
    isCustomPage: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    imageList() {
      let list = [];
      if (this.data && this.data.images && this.data.images.length) {
        list = this.data.images;
        list.sort((a, b) => a.sort - b.sort);
      }
      return list.slice(0, 2);
    }
  },
  methods: {
    goPage() {
      console.log('VR预览更多');
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: 'VR预览',
        modelCode: 'vr_preview',
        site_number: 'null',
        itemCode: '更多',
      })
      if (this.isCustomPage) {
        this.$router.push(`/shopVRPreview?pageModalId=${this.data.id}`);
      } else {
        this.$router.push(`/shopVRPreview?storeModalId=${this.data.id}`);
      }
    },
    toVrDetail(item, index) {
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: 'VR预览',
        modelCode: 'vr_preview',
        site_number: index + 1 + '',
        itemCode: item.id + '',
      })
      if (this.$util.isSgRn()) {
        window.postMessage(JSON.stringify({
          type: 'openOtherUrl',
          data: {
            newUrl: item.urlParam,
          }
        }));
      } else {
        this.$util.openNewWindow(item.urlParam, true);
      }
    }
  },
}
</script>
